import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

import { Comment } from './comment';
import { CommentService } from './comment.service';

@Component({
  selector: 'app-comment',
  templateUrl: './comment.component.html',
  styleUrls: [ './comment.component.css' ]
})

export class CommentComponent implements OnInit {
  @Input() comment: Comment;
  @Output() commentAdd = new EventEmitter();
  @Input() formId: number;
  @Output() formIdChange = new EventEmitter<number>();

  isShowComment: boolean;
  isShowAnswers: boolean;

  constructor(private commentService: CommentService) {
    this.isShowComment = true;
    this.isShowAnswers = true;
  }

  ngOnInit(): void {
    this.isShowComment = (this.comment.rating >= -10);
    this.comment.children.sort(function(a, b) {
      return a.timestamp - b.timestamp;
    });
  }

  dec(): void {
    this.comment.rating -= 1;
    this.commentService.updateRating(this.comment.timestamp, this.comment.rating);
    this.isShowComment = (this.comment.rating >= -10);
  }

  inc(): void {
    this.comment.rating += 1;
    this.commentService.updateRating(this.comment.timestamp, this.comment.rating);
    this.isShowComment = (this.comment.rating >= -10);
  }

  convertMS(ms): any {
    let d, h, m, s;
    s = Math.floor(ms / 1000);
    m = Math.floor(s / 60);
    s = s % 60;
    h = Math.floor(m / 60);
    m = m % 60;
    d = Math.floor(h / 24);
    h = h % 24;
    return { d: d, h: h, m: m, s: s };
  }

  getTimePeriod(): string {
    const period = this.convertMS(Date.now() - this.comment.timestamp);
    let result = '';
    if (period.m === 0) {
      result = 'Меньше минуты назад';
    } else if (period.m < 60) {
      result = period.m + ' мин. назад';
    } else if (period.m < (60 * 12)) {
      result = period.h + ' час(а) назад';
    } else if (period.m > (60 * 12)) {
      result = period.d + ' дня назад';
    }
    return result;
  }

  onCommentAdd(): void {
    this.setFormId(0);
    this.commentAdd.emit();
  }

  setFormId(id: number): void {
    this.formId = id;
    this.formIdChange.emit(id);
  }
}



