import { Injectable } from '@angular/core';

import { Comment } from './comment';

@Injectable()
export class CommentService {
  comments: Comment[] = [];

  getRootComments(): Promise<Comment[]> {
    this.comments.sort((a, b) => a.timestamp - b.timestamp);
    return Promise.resolve(this.comments.filter(item => item.level === 0));
  }

  addComment(name: string, email: string, text: string, parentId: number): void {
    const comment = {
      timestamp: Date.now(),
      name: name,
      email: email,
      text: text,
      level: 0,
      rating: 0,
      children: []
    };

    if (parentId !== 0) {
      for (let i = 0; i < this.comments.length; i += 1) {
        if (parentId === this.comments[i].timestamp) {
          comment.level = this.comments[i].level + 1;
          this.comments[i].children.push(comment);
          break;
        }
      }
      this.comments.push(comment);
    } else {
      this.comments.push(comment);
    }
  }

  updateRating(id: number, rating: number): void {
    for (let i = 0; i < this.comments.length; i += 1) {
      if (id === this.comments[i].timestamp) {
        this.comments[i].rating = rating;
        break;
      }
    }
  }
}



